.panel {
    background-color:white;
    color: #afc4d5;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 40px;
    width: 80%;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 80px;
    text-align: center;
    font-size: 24px;
    font-family: "Helvetica";
}

.panel:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor:pointer;

}


